import { mapState } from "vuex";

export default {
    data() {
        return {
            iphone_x: false,
            user: {},
            up_type: '',
            loading: false,
            canUp: false,
            up: {},
            success: false,
            page: 2,
            width: -25,
            setting: {},
            page_count: 1,
            list: []
        }
    },
    computed: {
        ...mapState({
            stock: state => state.mallConfig.__wxapp_img.stock,
            mall: state => state.mallConfig.mall,
            userInfo: state => state.user.info,
        })
    },
    methods: {
        getList() {
            let that = this;
            that.$request({
                url: that.$api.stock.level,
            }).then(response=>{
                if(response.code == 0) {
                    that.user = response.data.user;
                    that.up_type = response.data.up_type;
                    let list = response.data.list;
                    that.page_count = response.data.pagination.page_count;
                    let condition = that.user.condition.toString();
                    that.width = -20 - (condition.length*5);
                    let canUp = 0;
                    that.success = false;
                    if(list.length == 0) {
                        that.$hideLoading();
                    }else {
                        for(let i in list) {
                            list[i].up = true;
                            canUp++;
                            if(+list[i].condition > +that.user.condition) {
                                list[i].up = false;
                                canUp--;
                            }
                            if(i == list.length-1) {
                                setTimeout(function(){
                                    that.list = list;
                                    that.$hideLoading();
                                },200)
                            }
                        }
                    }
                    if(canUp > 0) {
                        that.canUp = true;
                    }
                }else {
                    that.$hideLoading();
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        toUpdate() {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.$request({
                url: that.$api.stock.up,
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.up = response.data;
                    this.success = true;
                    this.canUp = false;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            })
        },
        getMore() {
            if(this.loading) {
                return false;
            }
            if(this.page > this.page_count) {
                return false;
            }
            let that = this;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.loading = true;
            that.$request({
                url: that.$api.stock.level,
                data: {
                    page: this.page
                }
            }).then(response=>{
                this.loading = false;
                if(response.code == 0) {
                    let list = response.data.list;
                    let canUp = 0;
                    if(list.length == 0) {
                        uni.hideLoading();
                    }else {
                        that.page++;
                        for(let i in list) {
                            list[i].up = true;
                            canUp++;
                            if(+list[i].condition > +that.user.condition) {
                                list[i].up = false;
                                canUp--;
                            }
                            if(i == list.length-1) {
                                setTimeout(function(){
                                    that.list = that.list.concat(list);
                                    that.$forceUpdate();
                                    uni.hideLoading();
                                },200)
                            }
                        }
                    }
                    if(canUp > 0) {
                        that.canUp = true;
                    }
                }else {
                    uni.hideLoading();
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                this.loading = false;
                uni.hideLoading();
            });
        },
        getSetting() {
            let that = this;
            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            that.$request({
                url: that.$api.stock.setting,
            }).then(response=>{
                that.getList();
                if(response.code == 0) {
                    that.setting = response.data;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        },
    },
    onReachBottom() {
        this.getMore();
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        uni.getSystemInfo({
            success: function (res) {
                if(res.model.indexOf('iPhone X') > -1 || res.model.indexOf('iPhone 11') > -1 || res.model.indexOf('iPhone11') > -1 || res.model.indexOf('iPhone12') > -1 || res.model.indexOf('Unknown Device') > -1) {
                    that.iphone_x = true;
                }
            }
        })
        that.getSetting();
    }
}
